import { Component, OnInit } from '@angular/core';
import { PayrollBankOutService, PayrollPegawaiPayrollService } from 'src/app/sdk/payroll/services';
import { NzMessageService, NzNotificationService, NzDrawerService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { ActivatedRoute } from '@angular/router';
import { PesanService } from 'src/app/services/tool/pesan.service';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private spinner: NgxSpinnerService,
        private activatedRoute: ActivatedRoute,
        private payrollPegawaiPayrollService: PayrollPegawaiPayrollService,
        private payrollBankOutService: PayrollBankOutService,
        private pesan: PesanService,
        private notification: NzNotificationService,
        private drawerService: NzDrawerService
    ) { }




    periode: string = '';
    ngOnInit(): void {
        this.activatedRoute.paramMap.subscribe(
            data => {
                if (data.get('periode')) {
                    this.periode = data.get('periode');
                    this.getRekap()
                }
            }
        )
    }

    listData = [];
    report = [];
    nama_bank = [];
    getRekap() {
        this.spinner.show()
        this.payrollBankOutService.getPayrollBankOuts(JSON.stringify({ periode_3: this.periode })).subscribe(
            data => {
                this.spinner.hide()
                this.listData = data.data['pegawai'];
                this.report = data.data['report'];
                this.nama_bank = data.data['nama_bank'];

            }
        )
    }

    is_enable
    downloadPayement(type) {

    }



}
